body {
  background-color: #d9b3ff;
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.game {
  margin-top: 30px;
  display: "flex";
  justify-content: center;
  align-items: center;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background-color:  #d9b3ff;
  border: 1px solid #000000;
  float: left;
  font-size: 45px;
  font-weight: bold;
  line-height: 34px;
  height: 60px;
  width: 60px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}
